<script>
import tableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "companyProfileManagement-component",
  components: { tableComponent, Pagination1 },
  data() {
    return {
      search: "",
      data: [],
      tableData: [],
      tableList: [],
      searchData: [],
      selectedLists: [],
      tableColumns: [
        {
          id: "id",
          label: "項次",
          width: "70",
        },
        {
          id: "shortName_Id",
          label: "公司簡稱(統ㄧ編號)",
          width: "190",
        },
        {
          id: "fullName",
          label: "公司全稱(電話/發票住址)",
          width: "200",
        },
        {
          id: "admin",
          label: "帳號管理員(姓名/電話/身分證號碼)",
          width: "300",
        },
        {
          id: "email",
          label: "email",
          width: "300",
        },
      ],
    };
  },
  computed: {},
  methods: {
    async searchHandler(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.getSupplier(1, 20000, val);
        this.$store.state.currentPage = 1;
        this.$refs.TableComponent.$refs.multipleTable.clearSelection();
      }
    },
    changeSelection(item) {
      this.selectedLists = item;
    },
    // 檔案下載
    async exportVendor() {
      try {
        const ids = this.selectedLists.map((e) => e.s_Id);
        const obj = { S_Ids: ids };
        const res = this.$store.dispatch("vendorInfoExportVendor", obj);
        this.downloadFile(res, "廠商資料", "xlsx");
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
      this.$store.state.currentPage = page;
    },
    // API取得資料
    async getSupplier(current, count, word = null) {
      try {
        const obj = { current, count, word };
        const res = await this.$store.dispatch("vendorInfoGetSupplier", obj);
        const data = res.data.data;
        const copyData = JSON.parse(JSON.stringify(data.suppliers));
        const newData = this.forEachData(copyData);
        this.data = JSON.parse(JSON.stringify(newData));
        this.tableData = this.data;
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    async initVendorInfo() {
      try {
        const res = await this.$store.dispatch("initVendorInfo");
        const data = res.data.data;
        const copyData = JSON.parse(JSON.stringify(data.suppliers));
        const newData = this.forEachData(copyData);
        this.data = JSON.parse(JSON.stringify(newData));
        this.tableData = this.data;
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    // 資料整理
    forEachData(copyData) {
      copyData.forEach((e) => {
        if (e.admin_Name === null || e.admin_Name.trim() === "") {
          e.admin_Name = "無";
        }
        if (e.tel === null || e.tel.trim() === "") {
          e.tel = "無";
        }
        if (e.address1 === null || e.address1.trim() === "") {
          e.address1 = "無";
        }
        if (e.admin_Tel === null || e.admin_Tel.trim() === "") {
          e.admin_Tel = "無";
        }
        if (e.admin_Id === null || e.admin_Id.trim() === "") {
          e.admin_Id = "無";
        }
        e.id = e.item;
        e.shortName_Id = `${e.short_Name}(${e.s_Id})`;
        e.fullName = `${e.s_Name}/${e.tel}/${e.address1}`;
        e.admin = `${e.admin_Name}/${e.admin_Tel}/${e.admin_Id}`;
      });
      return copyData;
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    this.$store.state.currentPage = 1;
    // 身分是廠商時摳的API 一進去不需KEY IN 就顯示資料
    await this.initVendorInfo();
  },
};
</script>

<template src="./companyProfileManagement.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
